.Footer {
	background-color: #707070;
	/* top: -102px; */
	/* position: relative; */
	z-index: -1;
	color: #f7f7f7;
	font-family: omnes-pro, sans-serif;
}

#location {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	padding-top: 10px;
}

#location > a > img {
	width: 40px;
	height: 40px;
}

#location > p {
	margin: 0;
	font-size: 16px;
}

#footerMain {
	display: flex;
	padding: 20px;
	justify-content: space-between;
}

#footerLogo {
	width: 200px;
	height: auto;
}

#copyright {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

#copyright > div {
	display: flex;
	flex-direction: column;
	align-items: center;
}

#copyright > div > h3 {
	font-family: sauna-new, sans-serif;
	color: #f1bc5c;
	font-size: 36px;
	margin: 0;
}

#copyright > div > h4 {
	margin: 0;
	position: relative;
	bottom: 10px;
	font-weight: 400;
	font-size: 20px;
}

#copyright > div > p {
	font-size: 14px;
	margin: 0;
}

#desktopCopyright {
	display: block;
}

#mobileCopyright {
	display: none;
	margin: 0;
	padding: 15px;
}

@media (max-width: 600px) {
	#footerLogo {
		display: none;
	}

	#footerMain {
		display: flex;
		flex-direction: column-reverse;
	}

	#location {
		display: flex;
		align-items: center;
	}

	#desktopCopyright {
		display: none;
	}

	#mobileCopyright {
		display: block;
	}

	.Footer {
		max-width: 600px;
	}
}
